"use client";

import { useEffect, useMemo, useState } from "react";

type Row = {
  id: number;
  subproducto?: string;
  cuenta_bancaria?: string;
  monto?: string | number | null;
  importe_pagar?: string | number | null;
  fecha_vencimiento?: string | null;
  dias_vencidos?: number | null;

  // logo en temporal
  url?: string | null;
  logo_url?: string | null;

  // lo “raro” (uuid/token)
  uuid?: string | null;
  token?: string | null;
  public_id?: string | null;

  // si guardas link en BD
  link?: string | null;
};

const normalizeUrl = (u: any) => {
  if (!u) return null;
  let s = String(u).trim().replaceAll("\\", "/");

  // absoluta
  if (/^https?:\/\//i.test(s)) return s;

  // "public/uploads/x.png" => "/uploads/x.png"
  if (s.startsWith("public/uploads/")) s = s.replace("public/", "");
  if (s.startsWith("uploads/")) s = `/${s}`;

  if (s.startsWith("/")) return s;

  return `/${s}`;
};

// ✅ si tienes una ruta pública ya existente para abrir la liga,
// cámbiala aquí.
// Ejemplos:
// const PUBLIC_BASE_PATH = "/pago";
// const PUBLIC_BASE_PATH = "/liga";
const PUBLIC_BASE_PATH = "/pago";

function buildPublicLink(row: Row) {
  const origin = typeof window !== "undefined" ? window.location.origin : "";

  // 1) Si BD ya trae link, lo “limpiamos” para quitar localhost y usar origin actual
  if (row.link) {
    try {
      // si es absoluto, tomamos solo pathname + search
      if (/^https?:\/\//i.test(row.link)) {
        const u = new URL(row.link);
        return `${origin}${u.pathname}${u.search}${u.hash}`;
      }
      // si es relativo, lo pegamos al origin
      const rel = row.link.startsWith("/") ? row.link : `/${row.link}`;
      return `${origin}${rel}`;
    } catch {
      const rel = row.link.startsWith("/") ? row.link : `/${row.link}`;
      return `${origin}${rel}`;
    }
  }

  // 2) Si NO hay link, construimos con uuid/token/public_id
  const code =
    row.uuid?.trim() ||
    row.token?.trim() ||
    row.public_id?.trim() ||
    null;

  // si tienes code, usa /pago/<code>
  if (code) return `${origin}${PUBLIC_BASE_PATH}/${encodeURIComponent(code)}`;

  // 3) fallback (si solo tienes id)
  return `${origin}${PUBLIC_BASE_PATH}/${encodeURIComponent(String(row.id))}`;
}

export default function BaseClient() {
  const [rows, setRows] = useState<Row[]>([]);
  const [loading, setLoading] = useState(true);

  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState<Row | null>(null);

  const [toast, setToast] = useState<string>("");

  async function fetchList() {
    setLoading(true);
    try {
      const res = await fetch("/api/bases-pago/listar", { cache: "no-store" });
      const json = await res.json();
      setRows(Array.isArray(json) ? json : []);
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    fetchList();
  }, []);

  async function ver(id: number) {
    const res = await fetch(`/api/bases-pago/ver/${id}`, { cache: "no-store" });
    const json = await res.json();
    if (!res.ok) {
      setToast(json?.error || "Error al ver");
      return;
    }
    setSelected(json);
    setOpen(true);
  }

  async function borrarTodo() {
    const ok = confirm("¿Borrar TODAS las plantillas temporales?");
    if (!ok) return;

    const res = await fetch("/api/bases-pago/borrar-todo", { method: "DELETE" });
    const json = await res.json().catch(() => ({}));
    if (!res.ok) {
      setToast(json?.error || "No se pudo borrar todo");
      return;
    }
    setToast("Borradas");
    setOpen(false);
    setSelected(null);
    await fetchList();
  }

  async function copiarLink(row: Row) {
    const link = buildPublicLink(row);
    await navigator.clipboard.writeText(link);
    setToast("Link copiado");
    setTimeout(() => setToast(""), 1500);
  }

  const title = useMemo(() => "Bases (plantillas_temporales)", []);

  return (
    <div className="min-h-screen bg-[#0B1220] text-white p-6">
      <div className="max-w-5xl mx-auto">
        <div className="flex items-center justify-between gap-4">
          <div>
            <div className="text-3xl font-bold">{title}</div>
            <div className="text-sm opacity-70">Listado de ligas de pago guardadas.</div>
          </div>

          <button
            onClick={borrarTodo}
            className="px-4 py-2 rounded-lg bg-red-600 hover:bg-red-700"
            type="button"
          >
            Borrar todo
          </button>
        </div>

        {toast && (
          <div className="mt-4 px-4 py-2 rounded-lg bg-white/10 border border-white/10">
            {toast}
          </div>
        )}

        <div className="mt-6 bg-white/5 border border-white/10 rounded-2xl overflow-hidden">
          <div className="p-4 border-b border-white/10 flex items-center justify-between">
            <div className="font-semibold">Plantillas</div>
            <button
              onClick={fetchList}
              className="px-3 py-1.5 rounded-lg bg-white/10 hover:bg-white/20"
              type="button"
            >
              Refrescar
            </button>
          </div>

          {loading ? (
            <div className="p-6 opacity-80">Cargando…</div>
          ) : rows.length === 0 ? (
            <div className="p-6 opacity-80">No hay registros.</div>
          ) : (
            <div className="divide-y divide-white/10">
              {rows.map((r) => {
                const logo = r.logo_url || r.url || null;
                const logoSrc = logo ? normalizeUrl(logo) : null;

                return (
                  <div key={r.id} className="p-4 flex items-center justify-between gap-4">
                    <div className="flex items-center gap-3 min-w-0">
                      <div className="w-12 h-12 rounded-xl bg-white/10 overflow-hidden flex items-center justify-center">
                        {logoSrc ? (
                          <img
                            src={logoSrc}
                            alt="logo"
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="text-xs opacity-70">IMG</div>
                        )}
                      </div>

                      <div className="min-w-0">
                        <div className="font-semibold truncate">
                          {r.subproducto || "—"}
                        </div>
                        <div className="text-sm opacity-70 truncate">
                          Cuenta: {r.cuenta_bancaria || "—"}
                        </div>
                      </div>
                    </div>

                    <div className="flex items-center gap-2">
                      <button
                        onClick={() => copiarLink(r)}
                        className="px-3 py-2 rounded-lg bg-emerald-600 hover:bg-emerald-700"
                        type="button"
                      >
                        Link
                      </button>

                      <button
                        onClick={() => ver(r.id)}
                        className="px-3 py-2 rounded-lg bg-white/10 hover:bg-white/20"
                        type="button"
                      >
                        Ver
                      </button>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>

        {/* MODAL VER */}
        {open && selected && (
          <div className="fixed inset-0 bg-black/60 flex items-center justify-center p-4 z-50">
            <div className="w-full max-w-xl rounded-2xl bg-[#0F1B2E] border border-white/10 overflow-hidden">
              <div className="p-4 border-b border-white/10 flex items-center justify-between">
                <div className="font-semibold">Vista</div>
                <button
                  onClick={() => {
                    setOpen(false);
                    setSelected(null);
                  }}
                  className="px-3 py-1.5 rounded-lg bg-white/10 hover:bg-white/20"
                  type="button"
                >
                  Cerrar
                </button>
              </div>

              <div className="p-4 space-y-3">
                <div className="text-sm opacity-70">ID: {selected.id}</div>

                <div className="text-2xl font-bold">{selected.subproducto || "—"}</div>
                <div className="opacity-80">Cuenta: <b>{selected.cuenta_bancaria || "—"}</b></div>

                <div className="grid grid-cols-2 gap-3 mt-2">
                  <div className="bg-white/5 border border-white/10 rounded-xl p-3">
                    <div className="text-xs opacity-70">Monto</div>
                    <div className="font-semibold">{selected.monto ?? "—"}</div>
                  </div>
                  <div className="bg-white/5 border border-white/10 rounded-xl p-3">
                    <div className="text-xs opacity-70">Importe</div>
                    <div className="font-semibold">{selected.importe_pagar ?? "—"}</div>
                  </div>
                  <div className="bg-white/5 border border-white/10 rounded-xl p-3 col-span-2">
                    <div className="text-xs opacity-70">Vencimiento</div>
                    <div className="font-semibold">{selected.fecha_vencimiento || "—"}</div>
                  </div>
                </div>

                {/* ✅ EN VEZ DE MOSTRAR UUID: BOTÓN LINK */}
                <div className="bg-white/5 border border-white/10 rounded-xl p-3">
                  <div className="text-xs opacity-70 mb-2">Liga</div>

                  <div className="flex gap-2">
                    <input
                      readOnly
                      className="flex-1 bg-black/30 border border-white/10 rounded-lg px-3 py-2 text-sm"
                      value={buildPublicLink(selected)}
                    />
                    <button
                      onClick={() => copiarLink(selected)}
                      className="px-3 py-2 rounded-lg bg-emerald-600 hover:bg-emerald-700"
                      type="button"
                    >
                      Copiar
                    </button>
                  </div>
                </div>

                {/* Logo */}
                <div className="bg-white/5 border border-white/10 rounded-xl p-3">
                  <div className="text-xs opacity-70 mb-2">Logo</div>
                  <div className="w-20 h-20 rounded-2xl bg-white/10 overflow-hidden flex items-center justify-center">
                    {normalizeUrl(selected.logo_url || selected.url) ? (
                      <img
                        src={normalizeUrl(selected.logo_url || selected.url) as string}
                        alt="logo"
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <div className="text-xs opacity-70">IMG</div>
                    )}
                  </div>
                </div>

                <div className="flex justify-end pt-2">
                  <button
                    onClick={() => copiarLink(selected)}
                    className="px-4 py-2 rounded-lg bg-emerald-600 hover:bg-emerald-700"
                    type="button"
                  >
                    Link
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
